import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";

const Pagination = ({ itemsArray, category }) => {
  const [activePage, setActivePage] = useState(1);
  const [numberOfPages, setNumberOfPages] = useState(1);

  const history = useHistory();

  //   useEffect(() => {
  //     const urlParams = new URLSearchParams(window.location.search);
  //     urlParams.set("page", activePage);
  //     history.push(window.location.pathname + "?" + urlParams.toString());
  //   }, []);

  const pageEl = (numb) => {
    return (
      <li
        className={"page-item " + (numb === activePage && "active")}
        onClick={() => {
          setActivePage(numb);
        }}
      >
        <button to="/shop" className="page-link">
          {numb}
        </button>
      </li>
    );
  };

  const fn = () => {
    let as = [];
    const pagesCount = Math.ceil(itemsArray.length / 9);
    if (itemsArray.length > 0) {
      for (let i = 0; i < pagesCount; i++) {
        as.push(pageEl(i + 1));
      }
    }
    return as.map((i) => i);
  };

  useEffect(() => {
    const pagesCount = Math.ceil(itemsArray.length / 9);
    setNumberOfPages(pagesCount);
  }, []);

  return (
    <ul className="pagination">
      <li className="page-item">
        <button
          className="page-link"
          disabled={activePage === 1}
          onClick={() => {
            setActivePage((prev) => prev - 1);
          }}
        >
          Previous
        </button>
      </li>
      {fn()}
      <li className="page-item">
        <button
          className="page-link"
          disabled={activePage === numberOfPages}
          onClick={() => {
            setActivePage((prev) => prev + 1);
          }}
        >
          Next
        </button>
      </li>
    </ul>
  );
};

export default Pagination;
