import React from "react";
import { NavLink } from "react-router-dom";

import { Accordion, Card } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

const ItemsList = ({ setCategory }) => {
  return (
    <div className="col-md-3">
      <h2 className="mb-4">Categories</h2>
      <Accordion>
        <Card>
          <div className="card-header">
            <NavLink to="/shop" exact onClick={() => setCategory("all")}>
              Wszystkie produkty
            </NavLink>
          </div>
        </Card>
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <NavLink
              to="/shop/mens-clothing"
              className=""
              onClick={() => setCategory("men's clothing")}
            >
              Men's clothing
            </NavLink>
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/mens-clothing"
              >
                Tanie
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/mens-clothing"
              >
                Droższe
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/mens-clothing"
              >
                Super
              </NavLink>
            </Card.Body>
          </Accordion.Collapse>
        </Card>

        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="1">
            <NavLink
              to="/shop/jewelery"
              className=""
              onClick={() => setCategory("jewelery")}
            >
              Biżuteria
            </NavLink>
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="1">
            <Card.Body>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/jewelery"
              >
                Tanie
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/jewelery"
              >
                Droższe
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/jewelery"
              >
                Super
              </NavLink>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="2">
            <NavLink
              to="/shop/electronics"
              className=""
              onClick={() => setCategory("electronics")}
            >
              Elektronika
            </NavLink>
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="2">
            <Card.Body>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/electronics"
              >
                Tanie
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/electronics"
              >
                Droższe
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/electronics"
              >
                Super
              </NavLink>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="3">
            <NavLink
              to="/shop/womens-clothing"
              className=""
              onClick={() => setCategory("women's clothing")}
            >
              Women's clothing
            </NavLink>
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="3">
            <Card.Body>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/womens-clothing"
              >
                Tanie
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/womens-clothing"
              >
                Droższe
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action"
                to="/shop/womens-clothing"
              >
                Super
              </NavLink>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </div>
  );
};

export default ItemsList;
