import React, { useContext, useEffect } from "react";
// import { useHistory } from "react-router";
import { StoreContext } from "../../../store/StoreProvider";

const Item = ({ category, setItemsArray }) => {
  const { items } = useContext(StoreContext);
  //   const history = useHistory();

  const itemsPerPage = 9;

  const itemToShow = (title, description, type, price, id) =>
    items ? (
      <div className="col-md-4">
        <div className="card mb-4">
          <img src="/images/item.jpg" alt={title} className="card-img-top" />
          <div className="card-body">
            <h3 className="card-title mb-0">
              {title}
              {type === "new" && <span className="badge-success">New!</span>}
            </h3>
            <p className="">
              {description.substring(0, description.indexOf(".") + 1)}
            </p>
          </div>
          <div className="card-footer text-center">
            {price} $ id {id}
          </div>
        </div>
      </div>
    ) : (
      <p>
        Błąd podczas pobierania przedmiotów. Prosze odświeżyć stronę bądź
        spróbować później{" "}
      </p>
    );

  const errorMsg = (
    <p>
      Błąd podczas pobierania przedmiotów. Prosze odświeżyć stronę bądź
      spróbować później
    </p>
  );

  const itemsToShow =
    items &&
    items.map((item) => {
      const { title, description, type, price, id } = item;
      if (category === "all" || item.category === category) {
        return itemToShow(title, description, type, price, id);
      }
    });

  const fn = () => {
    if (items) {
      const itemsArray = itemsToShow.filter((i) => i !== undefined && i);
      if (itemsArray.length < itemsPerPage) {
        //   setItemsArray(itemsArray);
        return itemsArray;
      } else if (itemsArray.length >= itemsPerPage) {
        const smallArray = itemsArray.filter((i, index) => {
          if (index < itemsPerPage) {
            return i;
          }
        });
        //   setItemsArray(smallArray);
        return smallArray;
      } else {
        return errorMsg;
      }
    } else {
      return errorMsg;
    }
  };

  //   useEffect(() => {
  //     const urlParams = new URLSearchParams(window.location.search);
  //     urlParams.set("page", "1");
  //     history.push(window.location.pathname + "?" + urlParams.toString());
  //   }, []);

  return (
    <>
      {fn()}
      {/* {itemsToShow} */}
    </>
  );
};

export default Item;
